import React, { useContext } from 'react'
import { NavLink } from 'react-router-dom'
import classNames from 'classnames'

import { ThemeContext, LIGHT, DARK } from 'contexts'
import { routesMap } from 'routes'

import './toggler.css'

export default function Toggler({ reposCount }) {
  const [theme,] = useContext(ThemeContext)

  const navLinkColor = theme === DARK ? 'var(--my-silver)' : 'var(--bs-secondary)'

  const selected = classNames('border-bottom', 'border-2', 'border-danger', {
    [`text-${DARK}`]: theme === LIGHT,
    [`text-${LIGHT}`]: theme === DARK,
  })

  const badgeColor = classNames({
    [`bg-light`]: theme === LIGHT,
    [`bg-secondary`]: theme === DARK,
    [`text-${DARK}`]: theme === LIGHT,
    [`text-${LIGHT}`]: theme === DARK,
  })

  return (
    <nav className="toggler nav position-relative">
      {/* <NavLink to={routesMap.overview} className='nav-link text-decoration-none' activeClassName={selected} style={{ color: navLinkColor }}>
        <i className='bi-book me-2'></i>
        Обзор
      </NavLink> */}
      <NavLink to={routesMap.repositories} className='nav-link text-decoration-none w-auto' activeClassName={selected} style={{ color: navLinkColor }}>
        <i className='bi-journal-code me-2'></i>
      Репозитории
        {!!reposCount && (
          <small className={`badge rounded-pill ms-2 fw-normal ${badgeColor}`}>{reposCount}</small>
        )}
      </NavLink>
      <NavLink to={routesMap.projects} className='nav-link text-decoration-none w-auto' activeClassName={selected} style={{ color: navLinkColor }}>
        <i className="bi-kanban me-2"></i>
        Projects
      </NavLink>
    </nav>
  )
}